import {LOGIN} from '../constants';
import {LOGOUT} from '../constants';


export default function apilogin(state=[{isLoggedIn:false,message:"",userName:"",id:0}],action)
{

    switch(action.type)
    {
     case LOGIN:
            return [{isLoggedIn:action.payload.isLoggedIn,message:action.payload.message,userName:action.payload.userName,id:action.payload.id}] 
     case LOGOUT:
           return [{isLoggedIn:action.payload.logout}]

     default:
         return state 
         
         
         
    }
}