import {React,useState} from 'react';
import {makeStyles,AppBar,Toolbar,Typography,Button,IconButton} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import ListAllContainer from '../containers/ListAllContainer';
import Register from './Register';
import Login from './Login';
import UserEdit from './UserEdit';
import AddUser from './AddUser';
import {Switch,Route,NavLink} from "react-router-dom";

import LoginContainer from '../containers/LoginContainer';
import AddUserContainer from '../containers/AddUserContainer';
import UserEditContainer from '../containers/UserEditContainer';
import ProfileContainer from '../containers/ProfileContainer';
import TableContainer from '../containers/TableContainer';
import {useDispatch} from 'react-redux';
import {logoutUser} from '../service/Actions/actions';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));


const Home=(props)=>
{

const classes = useStyles();
const dispatch=useDispatch();
const [loggOut,setLoggOut]=useState(false);

const logout=(event)=>
{
let obj={
     logout:loggOut
        }
dispatch(logoutUser(obj));
}


return(<>

   <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
          </Typography>
         {props.data.apilogin[0].isLoggedIn===false?
         <>
           <Button color="inherit" ><NavLink to={`/register`} style={{ textDecoration: 'none',color:"white"}} >Register</NavLink></Button>
           <Button color="inherit" ><NavLink to={`/login`} style={{ textDecoration: 'none',color:"white"}}>Login</NavLink></Button>
         </>
          :
          <>
         <Button color="inherit"><NavLink to={`/listAll`} style={{ textDecoration: 'none',color:"white"}}>List All</NavLink></Button>
         <Button color="inherit"><NavLink to={`/table`} style={{ textDecoration: 'none',color:"white"}}>Table</NavLink></Button>     
         <Button color="inherit" onClick={logout}><NavLink to={`/login`} style={{ textDecoration: 'none',color:"white"}}>Logout</NavLink></Button>     
         <Button color="inherit">{props.data.apilogin[0].userName}</Button> 
          </>
         }
        </Toolbar>
      </AppBar>
    </div>

   <Switch> 
     <Route exact path="/listAll" component={ListAllContainer}/>
     <Route exact path="/login" component={LoginContainer}/>
     <Route exact path="/register" component={Register}/>
     <Route exact path="/adduser" component={AddUserContainer}/>
     <Route exact path="/userEdit" component={UserEditContainer}/>	
     <Route exact path="/profile" component={ProfileContainer}/>
     <Route exact path="/table" component={TableContainer}/>	
   </Switch>




</>);


}


export default Home;